import { Component, ViewEncapsulation } from '@angular/core';
import { GetLaugisticService } from '../services/GetLaugisticService';
import {GetOrderService} from '../services/GetOrderService';
import {PendingOrderService} from '../services/PendingOrderService';
import { Router, ActivatedRoute, Params } from '@angular/router';
import {GetLaugisticModel} from '../models/GetLaugisticModel';

@Component({
  selector: 'PendingOrder',
  styles: [ require('./PendingOrder.style.scss') ],
  template: require('./PendingOrder.template.html'),
  providers: [GetLaugisticService,PendingOrderService,GetOrderService],
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'login-page app'
  }
})
export class PendingOrderComponent {
  public isAdmin:boolean;
  public isChemist:boolean;
  public GetOrderServiceArray:GetOrderService[]=[];
  public GetLaugisticModelArray:GetLaugisticModel[]=[];
  public GetLaugisticModeldata:GetLaugisticModel[]=[];
  public username:string;
  public fullName:any;
  public Chemistusername:any;
  public Chemistpassword:any;
  public password:string;
  public router:Router;
  public AdminUserType:any;
  public ChemistUserType:any;
  public Lougistic_Id:any;
  public Order_Id:any;
  public UserType:any;
  public PendingOrderServiceArray:PendingOrderService[]=[];

  constructor(public _getLaugisticService: GetLaugisticService,public _getPendingOrderService: PendingOrderService,public _getOrderService: GetOrderService,  router: Router, ) {
    this.UserType=localStorage.getItem("UserType");
    if(this.UserType==null)
    {
      this.router.navigate(["/app/login"]);
    
    }
    else
    {
    this.router = router;
    this.username=undefined;
    this.password=undefined;
    this._getLaugisticService.GetLaugisticName().subscribe(
      response => {
        this.GetLaugisticModelArray = response.data;
        jQuery("#snackbar").html(response.message);
        this.myFunction();
  
      });
      this.pendingOrderFun();
      console.log(this.GetLaugisticModelArray);
    }
    }
  pendingOrderFun()
  {
    this._getOrderService.GetOrderDetailValueService().subscribe(
      response => {
        console.log(response.data);
        this.GetOrderServiceArray=response.data;
       
        jQuery("#snackbar").html(response.message);
        this.myFunction();

        
    }); 


  }
  GetLaugisticId(LougisticId)
  {
this.Lougistic_Id=LougisticId;
  }
  GetOrderId(OrderId)
  {
this.Order_Id=OrderId;
console.log(this.Order_Id);
  }
  AssignLogistic()
  {

    this._getPendingOrderService.AddLaugisticData(this.Order_Id,this.Lougistic_Id).subscribe(
      response => {
        this.GetLaugisticModeldata = response.data;
      
        jQuery("#snackbar").html(response.message);
      
        this.myFunction();
      
     
      });
    this.GetLaugisticModeldata=[];
      console.log(this.GetLaugisticModeldata);
    
  }

myFunction() {
    // Get the snackbar DIV
    var x = document.getElementById("snackbar")

    // Add the "show" class to DIV
    x.className = "show";

    // After 3 seconds, remove the show class from DIV
    setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
}



}



